.c-container {
  height: 100vh;
}

.c-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 70%;
}

.c-col {
  flex: 1;
  position: relative;
  margin: 0 10px;
  box-shadow: 0 0 10px #0000001f;
  transition: all 1s;
}

.c-col:hover {
  transform: scale(1.01);
}

.c-col img {
  width: 100%;
}

.category-content {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  top: 0;
}

.category-content p {
  font-size: 35px;
  text-shadow: 0 0 5px #000000;
  letter-spacing: 2px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 20px;
}

.category-content .c-btn {
  padding: 10px 15px;
  border: none;
  outline: none;
  text-transform: uppercase;
  font-weight: bold;
  background-color: #ffffff;
  letter-spacing: 1.4px;
  transition: all 0.5s;
}

.category-content .c-btn:hover {
  background-color: #ff0000;
}

@media screen and (max-width: 769px) {
  .c-row {
   flex-direction: column;
   height: 100%;
  }

  .c-col {
   width: 280px;
   height: auto;
   flex: unset;
   margin: 20px 0;
  }
}
